.o-buttons {
  display: inline-block;
  box-sizing: border-box;
  vertical-align: middle;
  margin: 0;
  outline: 0;
  border-style: solid;
  text-align: center;
  text-decoration: none;
  font-family: MetricWeb, sans-serif;
  font-weight: 600;
  border-radius: 0;
  cursor: pointer;
  transition: 0.3s background-color, 0.15s color ease-out, 0.15s border-color ease-out;
  user-select: none;
  background-clip: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-size: 16px;
  min-height: 26px;
  min-width: 26px;
  padding: 5px 8px 5px 8px;
  font-size: 14px;
  line-height: 14px;
  border-width: 1px;
  color: #17484C;
  background-color: transparent;
  border-color: rgba(38, 116, 122, 0.4); }
  .o-buttons[aria-selected=true], .o-buttons[aria-pressed=true] {
    color: #164447;
    background-color: rgba(43, 187, 191, 0.4);
    border-color: rgba(38, 116, 122, 0.5); }
  .o-buttons:not([disabled]):focus:not(:hover),
  .o-hoverable-on .o-buttons:not([disabled]):hover {
    background-color: rgba(43, 187, 191, 0.08);
    border-color: rgba(38, 116, 122, 0.5); }
  .o-buttons:not([disabled]):active {
    color: #164447;
    border-color: #f7e9d9; }
  .o-buttons[disabled] {
    pointer-events: none;
