@mixin oTeaserCollectionHeading {
	@include font-size(26, 26);
	position: relative;
	width: 100%;
	margin: 0;
	padding: 20px 0;
	font-family: $o-typography-sans;
	font-weight: normal;

	a {
		@include oTeaserCollectionHeadingLink;
	}

	&:before,
	&:after {
		border-top: 8px solid rgba(oColorsGetPaletteColor('black'), 0.05);
		content: '';
		left: 0;
		top: 0;
		position: absolute;
		width: 100%;
	}

	&:after {
		border-top-color: oColorsGetPaletteColor('black');

		@include oGridRespondTo(L) {
			width: 32.5%;
		}
	}

	&--full-width:after {
		width: 100%;
	}

	&--half-width:after {
		@include oGridRespondTo(L) {
			width: 49%;
		}
	}

	&--small {
		margin-bottom: 20px;
		border-bottom: 1px solid $_o-teaser-collection-color-bottom-border;

		&:after {
			width: 100%;
		}
	}
}

@mixin oTeaserCollectionHeadingLink {
	color: inherit;
	text-decoration: none;
	border: 0;

	&:hover {
		color: #777777;
	}

	&:after {
		@include oIconsGetIcon('arrow-right', oColorsGetPaletteColor('black'), 30, $iconset-version: 1);
		content: '';
		display: inline-block;
		position: relative;
		top: 8px;
		right: -13px;
		margin-left: -13px;
		margin-top: -10px;
	}
}

@mixin oTeaserCollectionItems {
	list-style: none;
	display: flex;
	flex-flow: column wrap;
	justify-content: flex-start;
	width: 100%;
	margin: 0;
	padding: 0;
}

@mixin oTeaserCollectionItemStretched {
	display: flex;
}
